import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/MainPage.vue'

Vue.use(VueRouter)

// 홈
// qr인증-정보
// qr결제- 정보
//
// 계좌관리
// QR내역
// 도움말
// alrt



export default new VueRouter({
    mode: 'history',
    routes:[


        {
            path: '/',
            name: 'Home',
            component: () => import('../views/MainPage.vue'),
        },
        {
        path: '/QrAuth',
        name: 'QrAuth',
        component: () => import('../views/QrAuthPage.vue'),
        },
        {
            path: '/QrPayment',
                name: 'QrPayment',
            component: () => import('../views/QrPaymentPage.vue'),
        },

        {
            path: '/QrHistoryPage',
                name: 'QrHistoryPage',
            component: () => import('../views/QrHistoryPage.vue'),
        },

        {
            path: '/Help',
                name: 'Help',
            component: () => import('../views/HelpPage.vue'),
        },
    ],

})



