import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import VueQrcodeReader from "vue-qrcode-reader";

Vue.config.devtools = true;
Vue.config.productionTip = false;

// eventBus 생성 - 컴포넌트간 통신 간편화를 위한 수단
export const eventBus = new Vue({
  methods: {
    qrRead(qrData) {
      console.log("qr read event bus")
      this.$emit('qrRead', qrData)
    },
    changePrice(priceInfo) {
      console.log("price change event bus")
      console.log(priceInfo)
      this.$emit('changePrice', priceInfo)
    },
    resAuthList(authList) {
      console.log("새로고침")
      console.log(authList)
      this.$emit('authList', authList)
    },
    resPayList(payList) {
      console.log("새로고침/resPayList")
      console.log(payList)
      this.$emit('payList', payList)
    },
    setMoney(data){
      console.log("price change event bus")
      console.log(data)
      this.$emit('settingMoney', data)
    }
  }
})

new Vue({
  VueQrcodeReader,
  router,
  store,
  vuetify,

  render: h => h(App)
}).$mount('#app')
