<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div>
        <v-container class="text-center">
            <v-col justify-center
                    class="pa-4" v-if="type == 'auth' && isSuccess == null"
                    cols="12"
                    sm="12"
                    md="12"
                    lg="12"
            >
                <h1>방문해주셔서 감사합니다</h1>
                <v-col><h3>예매된 QR코드로<br>승차해주시길 바랍니다.</h3></v-col>
            </v-col>
            <v-col

                    class="pa-4" v-if="type == 'pay' && isSuccess == null"
                    cols="12"
                    sm="12"
                    md="12"
                    lg="12"
            >
                <v-col><h1>요금안내</h1></v-col>
                <v-col><h3>예매된 QR코드로<br>승차해주시길 바랍니다.</h3></v-col>

                <v-col class="align-self-end">
                    <v-col
                            cols="12"
                            sm="12"
                            md="12"
                            lg="12"
                            class="text-h5 font-weight-bold"
                    >
                        성인 {{priceInfo.adult | comma}} 원
                    </v-col>
                    <v-col
                            cols="12"
                            sm="12"
                            md="12"
                            lg="12"
                            class="text-h5 font-weight-bold"
                    >
                        청소년 {{priceInfo.teenager | comma}} 원
                    </v-col>
                    <v-col
                            cols="12"
                            sm="12"
                            md="12"
                            lg="12"
                            class="text-h5 font-weight-bold">
                        어린이 {{priceInfo.child | comma}} 원
                    </v-col>
                </v-col>


            </v-col>
            <v-col
                    v-if="type == 'auth' && isSuccess == true"
                    cols="12"
                    sm="12"
                    md="12"
                    lg="12"
            >
                <v-col cols="12" >
                    <h1>예매번호    <b style="font-color: #000000">{{item.ticketNumber}}</b></h1>

                </v-col>
                <v-col cols="12" align="center"
                       justify="center">
                    <v-img
                            width="250"
                            contain
                            :src="require('../assets/QrSuccess.svg')"></v-img>
                </v-col>
                <v-col cols="12" >
                    <h1>좌석번호</h1>
                </v-col>
                <v-col class="">
                    <h1>{{item.seatNumber}}</h1>
                </v-col>
            </v-col>


            <v-col v-if="type == 'auth' && isSuccess == false"> <!-- Auth QR fail event -->

                <v-col cols="12">
                    <h1>
                        <b>인증실패</b>
                    </h1>
                </v-col>
                <v-col cols="12" align="center"
                       justify="center">
                    <v-img
                            width="250"
                            contain
                            :src="require('../assets/QrFail.svg')"></v-img>
                </v-col>
                <v-col cols="12" class="mt-10">
                    <h1>
                        <b>다시 시도 해주시길 바랍니다.</b>
                    </h1>

                </v-col>

            </v-col>                                      <!-- Auth QR fail event 끝 -->
            <v-col v-if="type == 'pay' && isSuccess == true">     <!-- Pay QR success event -->
                <v-col cols="12" class="mt-10"  align="center"
                       justify="center">
                    <v-img
                            width="250"
                            contain
                            :src="require('../assets/QrSuccess.svg')"></v-img>
                </v-col>
                <v-col cols="12" class="mt-10">
                    <h1>
                        <b style="font-size: 40px; font-color: #000000;">정상결제되었습니다.</b>
                    </h1>
                </v-col>

            </v-col>                                      <!-- pay QR success event 끝 -->
            <v-col v-if="type == 'pay' && isSuccess == false">  <!-- Pay QR fail event -->
                <v-col   style=" font-size:40px; font-color: #000000;"><b>결제실패</b></v-col>
                <v-col  align="center"
                        justify="center">
                    <v-img
                            width="250"
                            contain
                            :src="require('../assets/QrFail.svg')"></v-img>
                </v-col>
                <div style="margin-top: 10px;"><b style=" font-color: #000000;">다시 시도 해주시길 바랍니다.</b></div>
            </v-col>

        </v-container>
    </div>
</template>
<script>
import { eventBus } from "../main";
import axios from 'axios'

    export default {
        props: {
            type: {type: String, default: "", require: false},
            price: {type: String, default: "", require: false}
        },
        data() {
            return {
                isSuccess: null,
                item: ' ',
                priceInfo: {
                    adult: 0,
                    teenager: 0,
                    child: 0
                }
            }
        },
        filters: {
            comma(val) {
                return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            }
        },
        created() {
            // eventBus를 통해 QR코드 데이터를 전달받아서 Info에 적용
            eventBus.$on("qrRead", qrData => {
                console.log("qrData in QRInfo")
                console.log(qrData)

                this.item = JSON.parse(qrData);
                console.log(this.item);
                if (this.type == "pay") {   // 결제 타입이면 단순 정상결제 화면 표시
                    //
                    //TODO : axios 요청 영역?
                    console.log('포스트페이',this.item);
                    console.log( this.priceInfo);

                    if (this.item.seatNumber != null){
                        console.error(this.item.seatNumber);
                        this.showFail();
                        return;
                    }
                    let res = false;

                       if (this.item.type == 1){
                           this.item = Object.assign(this.item, {
                               price :this.priceInfo.child
                           });
                           if(parseInt(this.item.balance) < parseInt(this.item.price)){
                               console.error(this.item.balance , this.item.price);
                               this.showFail();
                               return;
                           }
                           res = axios.post('https://dev.olli-go.co.kr/kpay/payments',this.item)
                                .then(function (response) {
                                    const payList = response;
                                    console.log('[페이포스트결과값]',payList)
                                    eventBus.resPayList(payList);
                                    return true;
                                })
                                .catch(function(error){
                                    console.log(error)
                                    return false;
                                })

                           if(res === false){
                               this.showFail();
                           }else this.showSuccess();
                       }else  if (this.item.type == 2){
                           this.item = Object.assign(this.item, {
                               price :this.priceInfo.teenager
                           });
                           if(parseInt(this.item.balance) < parseInt(this.item.price)){
                               this.showFail();
                               return;
                           }
                           res = axios.post('https://dev.olli-go.co.kr/kpay/payments',this.item)
                               .then(function (response) {
                                   const payList = response;
                                   console.log('[페이포스트결과값]',payList)
                                   eventBus.resPayList(payList);
                                   return true;
                               })
                               .catch(function(error){
                                   console.log(error)
                                   return false;
                               })

                           if(res === false){
                               this.showFail();
                           }else this.showSuccess();


                       }else if (this.item.type == 3){
                           this.item = Object.assign(this.item, {
                               price :this.priceInfo.adult
                           });
                           if(parseInt(this.item.balance) < parseInt(this.item.price)){
                               this.showFail();
                               return;
                           }
                           res = axios.post('https://dev.olli-go.co.kr/kpay/payments',this.item)
                               .then(function (response) {
                                   const payList = response;
                                   console.log('[페이포스트결과값]',payList)
                                   eventBus.resPayList(payList);
                                   return true;
                               })
                               .catch(function(error){
                                   console.log(error)
                                   return false;
                               })
                           if(res === false){
                               this.showFail();
                           }else this.showSuccess();
                       }
                    // axios.post('https://dev.olli-go.co.kr/kpay/payments',
                    //     JSON.stringify(this.item))
                } else {

                    if (this.item.balance != null){
                        console.error(this.item.balance);
                        this.showFail();
                        return;
                    }

                    // 인증 타입이면 인증 정보와 함께 화면 표시
                    //TODO : axios 요청 영역?
                    // const authdata= this.item
                    console.log('포스트',this.item);
                    let res = axios.post('https://dev.olli-go.co.kr/kpay/auth', this.item)
                        .then(function (response) {
                            const authList = response;
                            console.log('전',authList)
                            eventBus.resAuthList(authList);
                            return true;
                        })
                        .catch(function (error) {
                            console.log(error);
                            return false;
                        });

                    if(res === false){
                        this.showFail();
                    }else this.showSuccess();
                }
            }),
            // eventBus를 통해 변동된 가격 정보를 요금안내 Info에 전달
            eventBus.$on("changePrice", priceInfo => {
                console.log("priceInfo in QRInfo");
                console.log(priceInfo);
                if(priceInfo) {
                    this.priceInfo = JSON.parse(priceInfo);
                    console.log(this.priceInfo)
                }
            }),
                eventBus.$on("settingMoney", data => {
                    console.log("priceInfo in QRInfo")
                    console.log(data)

                    // this.priceInfo = JSON.parse(priceInfo);
                    // console.log(this.priceInfo)
                })


        },

        components:{
        },
        methods:{
            modal(){
                this.showInsertModal=true
            },
            // 인증/결제 response success 시 1초간 보이는 화면
            showSuccess() {
                this.isSuccess = true;
                setTimeout(() => {
                    this.isSuccess = null;
                }, 10000);
            },
            // 인증/결제 response fail 시 1초간 보이는 화면
            showFail() {
                this.isSuccess = false;
                setTimeout(() => {
                    this.isSuccess = null;
                }, 3000);
            }
        }
    }
</script>
